import { StateCreator } from "zustand";

export interface GeolocationSlice {
  geoPermission: string;
  geoLoading: boolean;
  setGeoLoading: (geoLoading: boolean) => void;
  setGeoPermission: (geoPermission: string) => void;
}

export const createGeoloacationSlice: StateCreator<GeolocationSlice> = set => ({
  geoPermission: "",
  geoLoading: false,
  setGeoLoading: geoLoading => set(() => ({ geoLoading })),
  setGeoPermission: geoPermission => set(() => ({ geoPermission })),
});
