import { Routes, Route, HashRouter } from "react-router-dom";
import warehouses from "./data/warehouses.json";
import { useShallow } from "zustand/react/shallow";
import "./index.css";
import { useStore } from "./store/store";
import { Suspense, lazy } from "react";

const WarehousesPage = lazy(
  () => import("./view/warehouses-page/warehouses-page"),
);
const WarehouseListPage = lazy(
  () => import("./view/warehouse-list-page/warehouse-list-page"),
);
const WarehouseSchemaPage = lazy(
  () => import("./view/warehouse-schema-page/warehouse-schema-page"),
);
const CarPage = lazy(() => import("./view/car-page/car-page"));

export const App = () => {
  const { height, translate } = useStore(
    useShallow(state => ({
      height: state.height,
      translate: state.translate,
    })),
  );

  return (
    <Suspense fallback="">
      <div style={{ height: height, transform: translate }} className={"app"}>
        <HashRouter>
          <Routes>
            <Route path="/" element={<WarehousesPage />} />
            {warehouses.map(elem => (
              <Route
                path={`/${elem.query}`}
                key={elem.id}
                element={<WarehouseListPage />}
              />
            ))}
            <Route path="/:number" element={<CarPage />} />
            <Route path="/:id/schema" element={<WarehouseSchemaPage />} />
          </Routes>
        </HashRouter>
      </div>
    </Suspense>
  );
};
