import { ICar } from "src/api/car";
import {
  GET_WAITING_CAR,
  GET_UNLOADED_CAR,
  GET_WAREHOUSE_QUEUE,
} from "src/data/urls.json";
import { TWarehouseQuery } from "src/types/types";
import { quantitySort } from "src/utils/quantity-sort";
import { StateCreator } from "zustand";
const carDataInitState: ICar = {
  carNumber: "",
  carsQuantity: null,
  dischargeTypeId: NaN,
  dischargeTypeName: "",
  driverName: "",
  gateId: null,
  gateName: null,
  queueId: NaN,
  queueNumber: NaN,
  statusId: NaN,
  statusName: "",
  supplierId: NaN,
  supplierName: "",
  warehouseId: NaN,
  warehouseName: "",
};

export interface WarehousesSlice {
  houseData: ICar[] | [] | null;
  houseName: string | undefined;
  carNumber: string;
  carData: ICar;
  setHouseData: (houseData: ICar[] | [] | null) => void;
  setHouseName: (name: string | undefined) => void;
  setCarNumber: (number: string) => void;
  setCarData: (carData: ICar | undefined) => void;
  fetchHouseData: (query: TWarehouseQuery) => void;
  fetchCarData: (id: string) => void;
}

export const createWarehousesSlice: StateCreator<WarehousesSlice> = set => ({
  houseData: null,
  houseName: "",
  carNumber: "",
  carData: carDataInitState,
  fetchHouseData: async (query: TWarehouseQuery) => {
    try {
      const res = await fetch(`${GET_WAREHOUSE_QUEUE}?warehouseId=${query.id}`);
      const data = await res.json();
      set({
        houseData: data.sort((a: ICar, b: ICar) => quantitySort(a, b)),
        houseName: query.name,
      });
    } catch (e) {
      window.location.href = window.location.origin;
    }
  },
  fetchCarData: async (id: string) => {
    try {
      const res = await fetch(
        `${GET_WAITING_CAR}?carNumber=${id.toUpperCase()}`,
      );
      const data = await res.json();

      if (data.statusCode === 404) {
        const res = await fetch(
          `${GET_UNLOADED_CAR}?carNumber=${id.toUpperCase()}`,
        );
        const data = await res.json();

        if (data.statusCode === 404) {
          window.location.href = window.location.origin;
        } else {
          set({ carData: data, houseName: data.warehouseName });
        }
      } else {
        set({ carData: data, houseName: data.warehouseName });
      }
    } catch (e) {
      window.location.href = window.location.origin;
    }
  },
  setHouseData: houseData => set(() => ({ houseData })),
  setHouseName: houseName => set(() => ({ houseName })),
  setCarNumber: (carNumber: string) => set(() => ({ carNumber })),
  setCarData: (carData: ICar | undefined) => set(() => ({ carData })),
});
