import { create } from "zustand";
import { ModalSlice, createModalSlice } from "./modal.slice";
import { GeolocationSlice, createGeoloacationSlice } from "./geolocation.slice";
import { SearchSlice, createSearchSlice } from "./search.slice";
import { TranslationSlice, createTranslationSlice } from "./translation.slice";
import { WarehousesSlice, createWarehousesSlice } from "./warehouses.slice";
import { createCommonSlice, CommonSlice } from "./common.slice";
import { RecallSlice, createRecallSlice } from "./recall.slice";

export const useStore = create<
  ModalSlice &
    GeolocationSlice &
    SearchSlice &
    TranslationSlice &
    WarehousesSlice &
    CommonSlice &
    RecallSlice
>()((...a) => ({
  ...createModalSlice(...a),
  ...createGeoloacationSlice(...a),
  ...createSearchSlice(...a),
  ...createTranslationSlice(...a),
  ...createWarehousesSlice(...a),
  ...createCommonSlice(...a),
  ...createRecallSlice(...a),
}));
